import type { ComponentOptions } from 'vue';

function getComponentConfig(componentName: string, countryCode: string) {
    return {
        template: `<div><img :src="assetFilter('swaglanguagepack/static/flags/${countryCode}.svg')"></div>`,
        name: componentName,
        mounted() {
            console.warn(`DEPRECATED: Replace '<${componentName} />' with '<img :src="assetFilter('swaglanguagepack/static/flags/${countryCode}.svg')">'`)
        },
        computed: {
            assetFilter() {
                return Shopware.Filter.getByName('asset');
            },
        },
    } satisfies ComponentOptions;
}

for (const countryCode of countryCodes) {
    const componentName = `swag-language-pack-flag-${countryCode}`

    Shopware.Component.register(componentName, () => getComponentConfig(componentName, countryCode));
}
